<template>
<div class="list-group" style="width: 100%">
    <button draggable="true" @click='addToContainer(key)'  v-for="key in group.keys" :key="key.id" v-if="group.locks.includes(key.id)" :class="{'disabled-marker': store.state.map.usedKeys.includes(key.id) }" type="button" class="list-group-item list-group-item-action key-list-selector"> <img class="dot-selector" v-show="store.state.map.usedKeys.includes(key.id) " src="@/assets/havr-icon/dot-key-selector-disabled.svg"> 
    <img class="dot-selector" v-show="!store.state.map.usedKeys.includes(key.id)" src="@/assets/havr-icon/dot-key-selector.svg"> <span class="key-name">{{key.description}}</span></button>
</div>
</template>

<script>
import store from "@/flulux/store";
export default {
  props: ["group"],
  data() {
    return {
      store: store
    };
  },
  methods: {
    addToContainer(key) {
      this.store.state.map.currentFloor.markers.push({
        x: 50,
        y: 50,
        door_lock_id: key.id
      });
      this.store.state.map.usedKeys.push(key.id);
      this.$notify({
        message: "Key " + key.description + " added on map",
        type: "success",
        horizontalAlign: "right",
        timeout: 2000,
        verticalAlign: "top"
      });
    }
  }
};
</script>

<style scoped>
.key-list-selector {
  border: none;
  background: #fdfdfd;
}
.dot-selector {
  margin-bottom: 3px;
  margin-left: 5px;
}
.key-name {
  margin-left: 15px;
}

.disabled-marker {
  pointer-events: none;
  color: #dcdcdc;
}
</style>

