<template>
<div>
<div ref="content"  id='content' class="content d-flex justify-content-center" v-bind:style="{ backgroundImage: 'url(' + 'data:image/png;base64,'+ store.state.map.currentFloor.picture + ')', transform: 'scale(' + this.scale +')', 'background-position': 'center' }">
<img draggable="true" v-on:dragstart="drag(event, i)" v-for="(marker, i) in store.state.map.currentFloor.markers" :key="marker" :style="{left: marker.x + '%', top: marker.y + '%', position: 'absolute', 'margin-left': '-4em', 'cursor': 'pointer'}" @click="dragKey()" src="@/assets/havr-icon/key-marker.svg" class="key-marker"></div>
<nav class="zoom">
 <ul class="pagination">
    <li @click="zoom(true)"  class="page-item disabled zoom-paginator">
      <a class="page-link text-secondary" href="#" tabindex="-1">+</a>
    </li>
    <li @click="zoom(false)"  class="page-item disabled zoom-paginator">
      <a class="page-link text-secondary" href="#" tabindex="-1">-</a>
    </li>
  </ul>
</nav>
</div>

</template>

<script>
import store from "@/flulux/store";
import fabric from "fabric";
import $ from "jquery";
import ImageTracer from "imagetracerjs";
import lodash from "lodash";
import notify from "vue-notification";
export default {
  data() {
    return {
      store: store,
      scale: 0.8,
      currentMarkerDrag: null
    };
  },
  mounted() {
    this.showDrawer();
    var vm = this;
    this.$refs.content.addEventListener(
      "dragend",
      function(e) {
        console.log('i pass here')
        const offset = $(this).offset();
        const relativeX = e.pageX - offset.left;
        const relativeY = e.pageY - offset.top;
        if (
          relativeY / (vm.$refs.content.clientWidth * vm.scale) * 100 < 0 ||
          relativeY / (vm.$refs.content.clientWidth * vm.scale) * 100 > 100 ||
          relativeX / (vm.$refs.content.clientWidth * vm.scale) * 100 < 0 ||
          relativeX / (vm.$refs.content.clientWidth * vm.scale) * 100 > 100
        ) {
          vm.store.state.map.currentFloor.markers.splice(
            vm.currentMarkerDrag,
            1
          );
          vm.store.state.map.usedKeys.splice(
            vm.store.state.map.usedKeys.indexOf(vm.currentMarkerDrag, 1)
          );
          return vm.$notify({
            message: "Marker has been replaced in the list",
            type: "info",
            horizontalAlign: "right",
            timeout: 2000,
            verticalAlign: "top"
          });
        }
        vm.store.state.map.currentFloor.markers[vm.currentMarkerDrag].y =
          relativeY / (vm.$refs.content.clientWidth * vm.scale) * 100;
        vm.store.state.map.currentFloor.markers[vm.currentMarkerDrag].x =
          relativeX / (vm.$refs.content.clientWidth * vm.scale) * 100;
      },
      false
    );
  },
  methods: {
    async showDrawer(file) {
      this.store.actions.OPEN_GROUP_MAP_DRAWER(true);
    },
    drag(ev, i) {
      this.currentMarkerDrag = i;
    },
    zoom(param) {
      if (param) {
        this.scale = this.scale + 0.2;
      } else {
        this.scale = this.scale - 0.2;
      }
    }
  }
};
</script>

<style scoped>
.content {
  align-items: center;
  flex-wrap: wrap;
  background-repeat: no-repeat;
  width: calc(100% - 450px);
  background-size: 100% 100%;
  height: 1000px;
  left: 10%;
  position: absolute;
  overflow: auto;
}

.zoom-paginator {
  width: 40px;
  text-align: -webkit-center;
}

.zoom {
  position: fixed;
  bottom: 0;
  right: 0;
  cursor: pointer;
  margin-right: 380px;
}
</style>
