<template>
<div style="display:contents">
    <div @click="setGroup(group)" v-for="group in groups" :key="group.id" class="col-6">
        <div class="groups">
            <img v-if="group.lock_group_type === 'personal'" class="icon-map-group" src='@/assets/havr-icon/home_group.svg'>
             <img v-else class="icon-map-group" src='@/assets/havr-icon/business-group.svg'>
                    </div>
            <p class="group-title">{{group.description}}</p>
            <p class="group-title group-sub-title">{{group.locks.length}} keys</p>
        </div>
    </div>
</template>

<script>
import store from "@/flulux/store";
import repository from "@/repository/keychain";
import notifiy from "vue-notification";
import {
    userDataRepository
} from "@/repository/keychain";
export default {
    data() {
        return {
            store: store,
            groups: [],
            keychain: []
        };
    },
    methods: {
        async getGroups() {
            const data = await repository.getLockGroupsWithDoorlocks();
            this.groups = data.lock_groups;
            this.keychain = data.door_locks
        },
        setGroup(group) {
            if(group.locks.length === 0) {
                return this.$notify({
        message: 'You aint got keys attached to this group',
        type: "danger",
        timeout: 2000,
        horizontalAlign: "center",
        verticalAlign: "top"
      });
            }
            group.keys = this.keychain
            this.$root.$emit('SET_GROUP', group)
        }
    },
    mounted() {
        this.getGroups();
    }
};
</script>

